.recipe-container {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  color: rgb(84, 83, 83);
  padding: 40px 20px;
}

.label,
.ingredients {
  font-weight: 700;
  font-size: 20px;
  text-align: center;
  width: 400px;
}

.ingredients {
  margin-top: 40px;
  margin-bottom: 0;
}

.source {
  font-style: italic;
  font-weight: 400;
  font-size: 16px;
}

.calories {
  font-size: 18px;
}

.image {
  box-shadow: 0px 0px 8px rgb(116, 115, 115);
}

.ul {
  width: 320px;
  list-style: none;
  line-height: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

@media all and (max-width: 800px) {
  .label,
  .ingredients {
    font-size: 17px;
    width: 300px;
  }

  .calories {
    font-size: 16px;
  }
  
  .image {
    width: 250px;
  }
  
  .ul {
    width: 300px;
    line-height: 25px;
  }
}

@media all and (max-width: 500px) {
  .label,
  .ingredients {
    font-size: 16px;
    width: 200px;
  }

  .image {
    width: 200px;
  }
  
  .ul {
    width: 200px;
    line-height: 25px;
  }
}

@media all and (max-width: 300px) {
  .recipe-container {
    padding: 40px 6px;
  }

  .label,
  .ingredients {
    font-size: 16px;
    width: 170px;
  }
  
  .image {
    width: 170px;
  }
  
  .ul {
    width: 170px;
    line-height: 25px;
  }
}